<template>
  <div class="MediaView">
    <div class="aj"></div>
    <div class="current-user">현재 {{ currentUserNum }} 명 공부 중</div>
    <YoutubeMedia v-if="videoMode.youtube" @setTimer="setTimer" />
    <UserMedia v-if="videoMode.my" />
    <div class="video-mode">
      <div class="select-video-mode" ref="youtube" @click="videoModeHandler('youtube')">
        유튜브 비디오
      </div>
      <div class="select-video-mode" ref="my" @click="videoModeHandler('my')">
        내 비디오
      </div>
    </div>
    <MediaInput v-if="videoMode.my" />
    <table class="key-table" v-if="deviceType === 'PC'">
      <tr>
        <td>A</td>
        <td>S</td>
        <td>D</td>
        <td>Z</td>
        <td>J</td>
        <td>K</td>
        <td>L</td>
      </tr>
      <tr class="key-dsc">
        <td>이전 구간</td>
        <td>재생/정지</td>
        <td>다음 구간</td>
        <td>반복</td>
        <td>구간 반복</td>
        <td>자막 켜기/끄기</td>
        <td>자막 언어 변환</td>
      </tr>
    </table>
  </div>
</template>

<script>
  import YoutubeMedia from './YoutubeMedia'
  import UserMedia from './UserMedia'
  import MediaInput from '../MediaInput/MediaInput'
  import { mapState, mapMutations } from 'vuex'

  export default {
    components: {
      'YoutubeMedia': YoutubeMedia,
      'UserMedia': UserMedia,
      'MediaInput': MediaInput
    },
    data () {
      return {
        currentUserNum: 0,
        ranArray: [
          {
            min: 31,
            max: 37
          },
          {
            min: 26,
            max: 31
          },
          {
            min: 15,
            max: 18
          },
          {
            min: 8,
            max: 10
          },
          {
            min: 4,
            max: 6
          },
          {
            min: 2,
            max: 3
          },
          {
            min: 5,
            max: 7
          },
          {
            min: 17,
            max: 22
          },
          {
            min: 21,
            max: 27
          },
          {
            min: 40,
            max: 46
          },
          {
            min: 52,
            max: 59
          },
          {
            min: 47,
            max: 53
          },
          {
            min: 71,
            max: 76
          },
          {
            min: 120,
            max: 129
          },
          {
            min: 90,
            max: 99
          },
          {
            min: 130,
            max: 139
          },
          {
            min: 100,
            max: 105
          },
          {
            min: 95,
            max: 99
          },
          {
            min: 101,
            max: 106
          },
          {
            min: 150,
            max: 155
          },
          {
            min: 210,
            max: 215
          },
          {
            min: 240,
            max: 249
          },
          {
            min: 303,
            max: 312
          },
          {
            min: 321,
            max: 330
          }
        ]
      }
    },
    computed: {
      ...mapState([
        'videoMode',
        'deviceType'
      ])
    },
    created () {
      this.getCurrentUser()
      setInterval(() => {
        this.getCurrentUser()
      }, 20000)
      this.getLocalStorageVideoMode()
    },
    mounted () {
      this.convertColor()
    },
    updated () {
      this.convertColor()
    },
    methods: {
      ...mapMutations([
        'setVideoMode',
        'setTimer',
        'clearTimer',
        'setLocalStorageVideoMode',
        'getLocalStorageVideoMode'
      ]),
      videoModeHandler (mode) {
        if (mode === 'my') {
          this.clearTimer()
        }
        this.setVideoMode(mode)
        this.setLocalStorageVideoMode()
      },
      convertColor () {
        if (this.videoMode.youtube) {
          const youtube = this.$refs['youtube']
          youtube.style.backgroundColor = '#747474'
        } else {
          const youtube = this.$refs['youtube']
          youtube.style.backgroundColor = '#4C4C4C'
        }
        if (this.videoMode.my) {
          const my = this.$refs['my']
          my.style.backgroundColor = '#747474'
        } else {
          const my = this.$refs['my']
          my.style.backgroundColor = '#4C4C4C'
        }
      },
      getCurrentUser () {
        let hours = new Date().getHours()
        let min = this.ranArray[hours].min
        let max = this.ranArray[hours].max
        this.currentUserNum = this.generateRandom(min, max)
      },
      generateRandom (min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min
      }
    }
  }
</script>

<style>
  @import '../../styles/MediaView/MediaView.css'
</style>



